export default function SelectionSort(arr) {
  let animations = [];
  for (let i = 0; i < arr.length; i++) {
    let lowest = i;
    for (let j = i + 1; j < arr.length; j++) {
      var animate = {};
      animate.compare = [lowest, j, i];
      animations.push(animate);
      if (arr[j] < arr[lowest]) {
        lowest = j;
      }
      animate.compare = [lowest, j, i];
      animations.push(animate);
    }
    animate.swap = [i, lowest, arr[i], arr[lowest]];
    animations.push(animate);
    let temp = arr[i];
    arr[i] = arr[lowest];
    arr[lowest] = temp;
  }
  return animations;
}
